import React, { useState, useEffect } from "react";
import {
    Container,
    Row,
    Col,
    Card,
    Form,
    Button,
    Alert,
} from "react-bootstrap";
import { useDispatch } from "react-redux";
import {
    requestCodeEmail,
    requestPassRetri,
} from "../../../redux/requestApi/userAccountThunk";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import * as Yup from "yup";

// PasswordRetrieval component for password recovery process
// Component PasswordRetrieval  x l qu trnh ly li mt khu
const PasswordRetrieval = () => {
    const [email, setEmail] = useState("");
    const [code, setCode] = useState("");
    const [newPassword, setNewPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [errors, setErrors] = useState({});
    const [countdown, setCountdown] = useState(0);

    const dispatch = useDispatch();
    const navigate = useNavigate();

    // Countdown for resend code button
    // m ngc cho nt gi li m
    useEffect(() => {
        if (countdown > 0) {
            const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
            return () => clearTimeout(timer);
        }
    }, [countdown]);

    // Schema validation for sending code
    // Schema kim tra xc thc cho gi m
    const sendCodeSchema = Yup.object().shape({
        email: Yup.string()
            .email("Email khng ng nh dng.")
            .required("Email khng c b trng."),
    });

    // Schema validation for changing password
    // Schema kim tra xc thc cho thay i mt khu
    const changePasswordSchema = Yup.object().shape({
        email: Yup.string()
            .email("Email khng ng nh dng.")
            .required("Email khng c b trng."),
        code: Yup.string()
            .matches(/^\d{6}$/, "M xc thc phi l 6 ch s.")
            .required("M khng c  trng."),
        newPassword: Yup.string()
            .min(6, "Mt khu phi c t nht 6 k t.")
            .required("Mt khu mi khng c b trng."),
        confirmPassword: Yup.string()
            .required("Nhp li mt khu mi khng c b trng.")
            .oneOf(
                [Yup.ref("newPassword")],
                "Nhp li mt khu mi khng khp."
            ),
    });

    // Handle sending verification code
    // X l gi m xc thc
    const handleSendCode = () => {
        const formValues = { email: email.trim() };

        try {
            sendCodeSchema.validateSync(formValues, { abortEarly: false });
            setErrors({});
        } catch (validationError) {
            const newErrors = {};
            validationError.errors.forEach((error) => {
                if (error.includes("Email")) newErrors.email = error;
            });
            setErrors(newErrors);
            return;
        }

        dispatch(requestCodeEmail({ email: email.trim() }))
            .then((result) => {
                if (result.payload.EC === 0) {
                    toast.success(result.payload.EM);
                } else {
                    toast.error(result.payload.EM);
                }
            })
            .catch(() => {
                toast.error("C li xy ra khi gi m Code.");
            });
        setCountdown(60);
    };

    // Handle changing the password
    // X l thay i mt khu
    const handleChangePassword = () => {
        const formValues = {
            email: email.trim(),
            code: code.trim(),
            newPassword: newPassword.trim(),
            confirmPassword: confirmPassword.trim(),
        };

        try {
            changePasswordSchema.validateSync(formValues, {
                abortEarly: false,
            });
            setErrors({});
        } catch (validationError) {
            const newErrors = {};
            validationError.errors.forEach((error) => {
                if (error.includes("Email")) newErrors.email = error;
                if (error.includes("M")) newErrors.code = error;
                if (error.includes("Mt khu")) newErrors.newPassword = error;
                if (error.includes("Nhp li mt khu"))
                    newErrors.confirmPassword = error;
            });
            setErrors(newErrors);
            return;
        }

        dispatch(requestPassRetri(formValues))
            .then((result) => {
                if (result.payload.EC === 0) {
                    toast.success(result.payload.EM);
                    navigate("/dang-nhap");
                } else {
                    toast.error(result.payload.EM);
                }
            })
            .catch(() => {
                toast.error("C li xy ra khi i mt khu.");
            });
    };

    // Handle input changes and reset related errors
    // X l thay i u vo v t li li lin quan
    const handleInputChange = (field, value) => {
        switch (field) {
            case "email":
                setEmail(value);
                break;
            case "code":
                setCode(value);
                break;
            case "newPassword":
                setNewPassword(value);
                break;
            case "confirmPassword":
                setConfirmPassword(value);
                break;
            default:
                break;
        }

        if (errors[field]) {
            setErrors((prev) => ({ ...prev, [field]: "" }));
        }
    };
    return (
        <Container className="mt-4">
            <Row className="justify-content-center">
                <Col md={6}>
                    <Card body>
                        <h5 className="text-center">Ly Li Mt Khu</h5>

                        <Form>
                            <Form.Group controlId="email">
                                <Form.Label>
                                    Email{" "}
                                    <span style={{ color: "red" }}> *</span>
                                </Form.Label>
                                <div className="d-flex">
                                    <Form.Control
                                        type="email"
                                        value={email}
                                        onChange={(e) =>
                                            handleInputChange(
                                                "email",
                                                e.target.value
                                            )
                                        }
                                        placeholder="Nhp email ca bn "
                                    />
                                    <Button
                                        onClick={handleSendCode}
                                        disabled={countdown > 0}
                                        className="ms-2"
                                    >
                                        {countdown > 0
                                            ? `${countdown}s`
                                            : "Ly M Code"}
                                    </Button>
                                </div>
                                {errors.email && (
                                    <Alert variant="danger" className="mt-2">
                                        {errors.email}
                                    </Alert>
                                )}
                            </Form.Group>

                            <Form.Group controlId="code" className="mt-2">
                                <Form.Label>
                                    M Code{" "}
                                    <span style={{ color: "red" }}> *</span>
                                </Form.Label>
                                <Form.Control
                                    type="text"
                                    value={code}
                                    onChange={(e) =>
                                        handleInputChange(
                                            "code",
                                            e.target.value
                                        )
                                    }
                                    placeholder="Nhp m Code (6 ch s) t Email  xc nhn"
                                />
                                {errors.code && (
                                    <Alert variant="danger" className="mt-2">
                                        {errors.code}
                                    </Alert>
                                )}
                            </Form.Group>

                            <Form.Group
                                controlId="newPassword"
                                className="mt-2"
                            >
                                <Form.Label>
                                    Mt khu Mi{" "}
                                    <span style={{ color: "red" }}>
                                        {" "}
                                        * (t nht 6 k t)
                                    </span>
                                </Form.Label>
                                <Form.Control
                                    type="password"
                                    value={newPassword}
                                    onChange={(e) =>
                                        handleInputChange(
                                            "newPassword",
                                            e.target.value
                                        )
                                    }
                                    placeholder="Nhp mt khu mi"
                                />
                                {errors.newPassword && (
                                    <Alert variant="danger" className="mt-2">
                                        {errors.newPassword}
                                    </Alert>
                                )}
                            </Form.Group>

                            <Form.Group
                                controlId="confirmPassword"
                                className="mt-2"
                            >
                                <Form.Label>
                                    Nhp Li Mt khu Mi{" "}
                                    <span style={{ color: "red" }}> *</span>
                                </Form.Label>
                                <Form.Control
                                    type="password"
                                    value={confirmPassword}
                                    onChange={(e) =>
                                        handleInputChange(
                                            "confirmPassword",
                                            e.target.value
                                        )
                                    }
                                    placeholder="Nhp li mt khu mi"
                                />
                                {errors.confirmPassword && (
                                    <Alert variant="danger" className="mt-2">
                                        {errors.confirmPassword}
                                    </Alert>
                                )}
                            </Form.Group>

                            <Button
                                variant="success"
                                onClick={handleChangePassword}
                                className="mt-4"
                            >
                                i Mt Khu
                            </Button>
                        </Form>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};
export default PasswordRetrieval;
