import React, { useEffect, useState } from "react";
import { Form, Button, Col, Row, Alert } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { requestLogin } from "../../../redux/requestApi/userAccountThunk";
import * as Yup from "yup";

// Login component for user authentication
// Component ng nhp  xc thc ngi dng
const Login = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [errors, setErrors] = useState({});

    const loading = useSelector((state) => state.user?.loading);
    const accessToken = useSelector(
        (state) => state.user?.userAccount?.accessToken
    );

    const navigate = useNavigate();
    const dispatch = useDispatch();

    // Redirect to home if user is already logged in
    // Chuyn hng v trang ch nu ngi dng  ng nhp
    useEffect(() => {
        if (accessToken) {
            navigate("/");
        }
    }, [accessToken, navigate]);

    // Define validation schema using Yup
    // nh ngha schema kim tra xc thc bng Yup
    const validationSchema = Yup.object().shape({
        email: Yup.string()
            .email("Email khng ng nh dng.")
            .required("Email khng c b trng."),
        password: Yup.string().required("Mt khu khng c b trng."),
    });

    // Handle login process
    // X l qu trnh ng nhp
    const handleLogin = () => {
        const formValues = {
            email: email.trim(),
            password: password.trim(),
        };

        try {
            validationSchema.validateSync(formValues, { abortEarly: false });
            setErrors({});
        } catch (validationError) {
            const newErrors = {};
            validationError.errors.forEach((error) => {
                if (error.includes("Email")) newErrors.email = error;
                if (error.includes("Mt khu")) newErrors.password = error;
            });
            setErrors(newErrors);
            return;
        }

        dispatch(requestLogin(formValues))
            .then((result) => {
                if (result.payload.EC === 0) {
                    navigate("/");
                } else {
                    toast.error(result.payload.EM);
                }
            })
            .catch((error) => {
                toast.error("C li xy ra khi ng nhp.");
            });
    };

    // Handle input changes and reset related errors
    // X l thay i u vo v t li li lin quan
    const handleInputChange = (field, value) => {
        switch (field) {
            case "email":
                setEmail(value);
                break;
            case "password":
                setPassword(value);
                break;
            default:
                break;
        }

        // Clear errors related to the specific field
        // Xa li lin quan n trng c th
        if (errors[field]) {
            setErrors((prev) => ({ ...prev, [field]: "" }));
        }
    };

    // Handle Enter key press to submit form
    // X l s kin nhn phm Enter  gi form
    const handleKeyPress = (e) => {
        if (e.key === "Enter") {
            handleLogin();
        }
    };

    return (
        <Row className="justify-content-center">
            <Col xs={12} md={6}>
                <Form
                    className="mt-4 mb-4 p-4 border rounded-3 bg-light"
                    onKeyPress={handleKeyPress}
                >
                    <h2 className="text-center">ng nhp</h2>

                    <Form.Group controlId="email">
                        <Form.Label className="mt-2">
                            Email <span style={{ color: "red" }}> *</span>
                        </Form.Label>
                        <Form.Control
                            type="email"
                            value={email}
                            onChange={(e) =>
                                handleInputChange("email", e.target.value)
                            }
                        />
                        {errors.email && (
                            <Alert variant="danger" className="mt-2">
                                {errors.email}
                            </Alert>
                        )}
                    </Form.Group>

                    <Form.Group controlId="password">
                        <Form.Label className="mt-2">
                            Mt khu <span style={{ color: "red" }}> *</span>
                        </Form.Label>
                        <Form.Control
                            type="password"
                            value={password}
                            onChange={(e) =>
                                handleInputChange("password", e.target.value)
                            }
                        />
                        {errors.password && (
                            <Alert variant="danger" className="mt-2">
                                {errors.password}
                            </Alert>
                        )}
                    </Form.Group>

                    <div className="d-flex justify-content-center flex-column">
                        <Button
                            variant="success"
                            onClick={handleLogin}
                            disabled={loading}
                            className="mt-3 mb-1"
                        >
                            ng nhp
                        </Button>

                        <p className="mt-3">
                            Bn cha c ti khon?{" "}
                            <Link to="/dang-ky">ng k ti y</Link>.
                        </p>

                        <p>
                            Qun mt khu?{" "}
                            <Link to="/quen-mat-khau">Ly li ti y</Link>.
                        </p>
                    </div>
                </Form>
            </Col>
        </Row>
    );
};

export default Login;
