import React, { useEffect, useState } from "react";
import {
    Form,
    Button,
    Col,
    Row,
    Container,
    Alert,
    Modal,
} from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
    requestRegister,
    requestVerifyCodeRegister,
    requestCodeEmail,
} from "../../../redux/requestApi/userAccountThunk";
import { toast } from "react-toastify";
import * as Yup from "yup";

// Register component for user registration process
// Component ng k  x l qu trnh ng k ngi dng
const Register = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [fullName, setFullName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [errors, setErrors] = useState({});
    const [showModal, setShowModal] = useState(false);
    const [code, setCode] = useState("");
    const [timer, setTimer] = useState(60);
    const [canResend, setCanResend] = useState(false);

    const loading = useSelector((state) => state.user.loading);
    const accessToken = useSelector(
        (state) => state.user?.userAccount?.accessToken
    );

    // Redirect to home if the user is already logged in
    // Chuyn hng v trang ch nu ngi dng  ng nhp
    useEffect(() => {
        if (accessToken) {
            navigate("/");
        }
    }, [accessToken, navigate]);

    // Define validation schema using Yup
    // nh ngha schema kim tra xc thc bng Yup
    const validationSchema = Yup.object().shape({
        fullName: Yup.string()
            .required("Tn y  khng c b trng.")
            .max(23, "Tn y  ti a l 23 k t."),
        email: Yup.string()
            .email("Email khng ng nh dng.")
            .required("Email khng c b trng."),
        password: Yup.string()
            .min(6, "Mt khu phi c t nht 6 k t.")
            .required("Mt khu khng c b trng."),
        confirmPassword: Yup.string()
            .required("Nhp li mt khu mi khng c b trng.")
            .oneOf([Yup.ref("password")], "Nhp li mt khu mi khng khp."),
    });

    // Handle register button click
    // X l khi nhn nt ng k
    const handleRegisterClick = () => {
        const formValues = {
            fullName: fullName.trim(),
            email: email.trim(),
            password: password.trim(),
            confirmPassword: confirmPassword.trim(),
        };

        try {
            validationSchema.validateSync(formValues, { abortEarly: false });
            setErrors({});
        } catch (validationError) {
            const newErrors = {};
            validationError.errors.forEach((error) => {
                if (error.includes("Tn y ")) newErrors.fullName = error;
                if (error.includes("Email")) newErrors.email = error;
                if (error.includes("Mt khu")) newErrors.password = error;
                if (error.includes("Nhp li mt khu"))
                    newErrors.confirmPassword = error;
            });
            setErrors(newErrors);
            return;
        }

        // Dispatch register request
        // Gi yu cu ng k
        dispatch(requestRegister(formValues))
            .then((result) => {
                if (result.payload.EC === 0) {
                    toast.success(result.payload.EM);
                    setShowModal(true);
                    setTimer(60);
                    setCanResend(false);
                } else {
                    toast.error(result.payload.EM);
                }
            })
            .catch(() => {
                toast.error("C li xy ra khi ng k.");
            });
    };

    // Handle verification code submission
    // X l gi m xc thc
    const handleVerifyCodeSubmit = async (event) => {
        event.preventDefault();
        dispatch(requestVerifyCodeRegister({ code, email }))
            .then((result) => {
                if (result.payload.EC === 0) {
                    toast.success(result.payload.EM);
                    setShowModal(false);
                    navigate("/dang-nhap");
                } else {
                    toast.error(result.payload.EM);
                }
            })
            .catch(() => {
                toast.error("C li khi xc minh email.");
            });
    };

    // Handle resend verification code
    // X l gi li m xc thc
    const handleResendCode = () => {
        setTimer(60);
        setCanResend(false);
        dispatch(requestCodeEmail({ email: email }))
            .then((result) => {
                if (result.payload.EC === 0) {
                    toast.success("M xc thc  c gi li.");
                } else {
                    toast.error(
                        "Khng th gi li m xc thc. Vui lng th li."
                    );
                }
            })
            .catch(() => {
                toast.error("C li khi gi li m xc thc.");
            });
    };

    // Timer for resend code button
    // Hn gi cho nt gi li m
    useEffect(() => {
        let interval;
        if (timer > 0) {
            interval = setInterval(() => {
                setTimer((prevTimer) => prevTimer - 1);
            }, 1000);
        } else {
            setCanResend(true);
            clearInterval(interval);
        }
        return () => clearInterval(interval);
    }, [timer]);

    // Handle input field changes and clear related errors
    // X l thay i cc trng nhp v xa li lin quan
    const handleInputChange = (field, value) => {
        switch (field) {
            case "fullName":
                setFullName(value);
                break;
            case "email":
                setEmail(value);
                break;
            case "password":
                setPassword(value);
                break;
            case "confirmPassword":
                setConfirmPassword(value);
                break;
            default:
                break;
        }

        // Clear errors related to the specific field
        // Xa li lin quan n trng c th
        if (errors[field]) {
            setErrors((prev) => ({ ...prev, [field]: "" }));
        }
    };

    return (
        <>
            <Row className="justify-content-center">
                <Col xs={12} md={6}>
                    <Form className="mt-4 mb-4 p-4 border rounded-3 bg-light">
                        <h2 className="text-center">ng k</h2>

                        <Form.Group controlId="fullName">
                            <Form.Label className="mt-2">
                                Tn y {" "}
                                <span style={{ color: "red" }}> *</span>
                            </Form.Label>
                            <Form.Control
                                type="text"
                                value={fullName}
                                onChange={(e) =>
                                    handleInputChange(
                                        "fullName",
                                        e.target.value
                                    )
                                }
                            />
                            {errors.fullName && (
                                <Alert variant="danger" className="mt-2">
                                    {errors.fullName}
                                </Alert>
                            )}
                        </Form.Group>

                        <Form.Group controlId="email">
                            <Form.Label className="mt-2">
                                Email <span style={{ color: "red" }}> *</span>
                            </Form.Label>
                            <Form.Control
                                type="email"
                                value={email}
                                onChange={(e) =>
                                    handleInputChange("email", e.target.value)
                                }
                            />
                            {errors.email && (
                                <Alert variant="danger" className="mt-2">
                                    {errors.email}
                                </Alert>
                            )}
                        </Form.Group>

                        <Form.Group controlId="password">
                            <Form.Label className="mt-2">
                                Mt khu{" "}
                                <span style={{ color: "red" }}>
                                    {" "}
                                    * (t nht 6 k t)
                                </span>
                            </Form.Label>
                            <Form.Control
                                type="password"
                                value={password}
                                onChange={(e) =>
                                    handleInputChange(
                                        "password",
                                        e.target.value
                                    )
                                }
                            />
                            {errors.password && (
                                <Alert variant="danger" className="mt-2">
                                    {errors.password}
                                </Alert>
                            )}
                        </Form.Group>

                        <Form.Group controlId="confirmPassword">
                            <Form.Label className="mt-2">
                                Nhp li mt khu{" "}
                                <span style={{ color: "red" }}> *</span>
                            </Form.Label>
                            <Form.Control
                                type="password"
                                value={confirmPassword}
                                onChange={(e) =>
                                    handleInputChange(
                                        "confirmPassword",
                                        e.target.value
                                    )
                                }
                            />
                            {errors.confirmPassword && (
                                <Alert variant="danger" className="mt-2">
                                    {errors.confirmPassword}
                                </Alert>
                            )}
                        </Form.Group>

                        <div className="d-flex justify-content-center flex-column">
                            <Button
                                variant="success"
                                onClick={handleRegisterClick}
                                disabled={loading}
                                className="mt-3 mb-1"
                            >
                                ng k
                            </Button>

                            <p className="mt-3">
                                Bn  c ti khon?{" "}
                                <Link to="/dang-nhap">ng nhp ti y</Link>.
                            </p>
                        </div>
                    </Form>
                </Col>
            </Row>

            {/* Modal for Email Verification */}
            <Modal
                show={showModal}
                onHide={() => setShowModal(false)}
                backdrop="static"
                keyboard={false}
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title>Xc thc Email</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={handleVerifyCodeSubmit}>
                        <Form.Group>
                            <Form.Label>
                                Nhp m Code m bn nhn t Email
                            </Form.Label>
                            <Form.Control
                                type="text"
                                value={code}
                                onChange={(e) => setCode(e.target.value)}
                                placeholder="M code 6 ch s"
                                required
                            />
                        </Form.Group>
                        <Button
                            className="mt-2"
                            variant="primary"
                            type="submit"
                        >
                            Xc thc
                        </Button>
                        <Button
                            className="mt-2 ms-2"
                            variant="secondary"
                            onClick={handleResendCode}
                            disabled={!canResend}
                        >
                            {canResend
                                ? "Gi li m"
                                : `Gi li m sau ${timer}s`}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </>
    );
};

export default Register;
