import React, { useEffect, useState } from "react";
import {
    Container,
    Row,
    Col,
    Form,
    Button,
    Card,
    Tooltip,
    OverlayTrigger,
    Modal,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { requestPostScam } from "../../../redux/requestApi/usersApiThunk";
import { toast } from "react-toastify";
import { jwtDecode } from "jwt-decode";
import * as Yup from "yup";

// Validation schema for account details
// Schema xc thc cho chi tit ti khon
const accountSchema = Yup.object().shape({
    accountNumber: Yup.string()
        .matches(/^\d+$/, "S Ti Khon ch cha s")
        .required("S Ti Khon l bt buc"),
    accountName: Yup.string()
        .matches(
            /^[a-zA-Z\s]+$/,
            "Tn Ch Ti Khon ch cha ch ci khng du"
        )
        .required("Tn Ch Ti Khon l bt buc"),
    bankName: Yup.string()
        .required("Tn H Thng Thanh Ton l bt buc")
        .matches(
            /^[a-zA-Z\s]+$/,
            "Tn H Thng Thanh Ton ch cha ch ci khng du"
        ),
    evidenceLink: Yup.string()
        .url("Link minh chng khng hp l")
        .required("Link minh chng l bt buc"),
    advice: Yup.string().required("Li khuyn l bt buc"),
});

// PostScam component for reporting scam accounts
// Component PostScam  bo co cc ti khon la o
const PostScam = () => {
    const accessToken = useSelector(
        (state) => state.user?.userAccount?.accessToken
    );

    const loading = useSelector((state) => state.users?.loading);

    const [accounts, setAccounts] = useState([]);
    const [newAccount, setNewAccount] = useState({
        accountNumber: "",
        accountName: "",
        bankName: "",
        evidenceLink: "",
        advice: "",
    });

    const dispatch = useDispatch();

    const [showModal, setShowModal] = useState(false);
    const [modalIndex, setModalIndex] = useState(null);

    // Generate a random math problem for verification
    // To bi ton ngu nhin  xc minh
    const [num1, setNum1] = useState(0);
    const [num2, setNum2] = useState(0);
    const [userAnswer, setUserAnswer] = useState("");

    const createMathProblem = () => {
        const n1 = Math.floor(Math.random() * 10);
        const n2 = Math.floor(Math.random() * 10);

        setNum1(n1);
        setNum2(n2);
    };

    useEffect(createMathProblem, []);

    // Update new account details
    // Cp nht chi tit ti khon mi
    const updateNewAccount = async (field, value) => {
        setNewAccount({
            ...newAccount,
            [field]: value,
        });
    };

    // Add a new account to the list
    // Thm ti khon mi vo danh sch
    const addAccount = async () => {
        try {
            // Trim and validate data
            const trimmedAccount = {
                accountNumber: newAccount.accountNumber.trim(),
                accountName: newAccount.accountName.trim(),
                bankName: newAccount.bankName.trim(),
                evidenceLink: newAccount.evidenceLink.trim(),
                advice: newAccount.advice.trim(),
            };

            await accountSchema.validate(trimmedAccount);

            setAccounts([...accounts, trimmedAccount]);

            setNewAccount({
                accountNumber: "",
                accountName: "",
                bankName: "",
                evidenceLink: "",
                advice: "",
            });
        } catch (error) {
            toast.warning(error.message);
        }
    };

    // Update an existing account in the list
    // Cp nht ti khon hin c trong danh sch
    const updateAccount = (index, field, value) => {
        const updatedAccounts = accounts.slice();
        updatedAccounts[index][field] = value;
        setAccounts(updatedAccounts);
    };

    // Validate and save the account details
    // Xc thc v lu chi tit ti khon
    const validateAndSaveAccount = async (index) => {
        try {
            await accountSchema.validate(accounts[index]);
            const updatedAccounts = accounts.slice();
            updatedAccounts[index].isEditing = false;
            setAccounts(updatedAccounts);
        } catch (error) {
            toast.error(error.message);
            throw error;
        }
    };

    // Delete an account from the list
    // Xa mt ti khon khi danh sch
    const deleteAccount = (index) => {
        setModalIndex(index);
        setShowModal(true);
    };

    // Confirm deletion of an account
    // Xc nhn xa ti khon
    const confirmDelete = () => {
        const updatedAccounts = accounts.slice();
        updatedAccounts.splice(modalIndex, 1);
        setAccounts(updatedAccounts);
        setShowModal(false);
        setModalIndex(null);
    };

    // Toggle the editing state of an account
    // Chuyn i trng thi chnh sa ca ti khon
    const toggleEdit = (index) => {
        const updatedAccounts = accounts.map((account, idx) => {
            if (idx === index) {
                return { ...account, isEditing: !account.isEditing };
            }
            return account;
        });
        setAccounts(updatedAccounts);
    };

    // Handle form submission
    // X l gi form
    const handleSubmit = async () => {
        const isEditing = accounts.some((account) => account.isEditing);

        if (isEditing) {
            toast.warning(
                "Vui lng hon thnh chnh sa tt c cc ti khon trc khi gi."
            );
            return;
        }

        if (accounts.length === 0) {
            toast.warning("Khng c g  gi i.");
            return;
        }

        if (parseInt(userAnswer) !== num1 + num2) {
            toast.error("Vui lng gii ng bi ton.");
            return;
        }

        try {
            const decodedToken = jwtDecode(accessToken);
            const userId = decodedToken.id;

            // Loi b trng isEditing trc khi gi
            const payloadAccounts = accounts.map(
                ({ isEditing, ...account }) => account
            );
            const payload = { accounts: payloadAccounts, userId };

            dispatch(requestPostScam(payload))
                .then((result) => {
                    if (result.payload.EC === 0) {
                        toast.success(result.payload.EM);
                    } else {
                        toast.error(result.payload.EM);
                    }
                })
                .catch((error) => {
                    toast.error("C li xy ra khi ng bi.");
                });

            createMathProblem();
            setUserAnswer("");
            setAccounts([]);
        } catch (error) {
            toast.error("C li xy ra khi trch xut ID ngi dng.");
        }
    };

    return (
        <Container>
            <div className="main-template">
                <Card className="mb-3">
                    <Card.Body>
                        <Row>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group>
                                    <Form.Label className="mt-1">
                                        S Ti Khon
                                    </Form.Label>
                                    <OverlayTrigger
                                        placement="top"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={
                                            <Tooltip>
                                                Ngoi s Ngn Hng, bn c th
                                                nhp s MoMo, ZaloPay, in
                                                Thoi, v.v.
                                            </Tooltip>
                                        }
                                    >
                                        <Form.Label>
                                            <i
                                                className="bi bi-info-circle-fill ms-1"
                                                style={{ color: "red" }}
                                            ></i>
                                        </Form.Label>
                                    </OverlayTrigger>
                                    <Form.Control
                                        type="text"
                                        placeholder="V d: 1234567891011"
                                        value={newAccount.accountNumber}
                                        onChange={(e) =>
                                            updateNewAccount(
                                                "accountNumber",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group>
                                    <Form.Label className="mt-1">
                                        Tn Ch Ti Khon
                                    </Form.Label>
                                    <OverlayTrigger
                                        placement="top"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={
                                            <Tooltip>
                                                Ngoi tn ch ngn hng, bn c
                                                th nhp tn MoMo, ZaloPay, ST
                                                v.v. (khng du)
                                            </Tooltip>
                                        }
                                    >
                                        <Form.Label>
                                            <i
                                                className="bi bi-info-circle-fill ms-1"
                                                style={{ color: "red" }}
                                            ></i>
                                        </Form.Label>
                                    </OverlayTrigger>
                                    <Form.Control
                                        type="text"
                                        placeholder="V d: nguyen van a"
                                        value={newAccount.accountName}
                                        onChange={(e) =>
                                            updateNewAccount(
                                                "accountName",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group>
                                    <Form.Label className="mt-1">
                                        Tn H Thng Thanh Ton
                                    </Form.Label>
                                    <OverlayTrigger
                                        placement="top"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={
                                            <Tooltip>
                                                Ngoi h thng ngn hng, bn c
                                                th nhp h thng MoMo, ZaloPay,
                                                SHIPCODE v.v. (khng du)
                                            </Tooltip>
                                        }
                                    >
                                        <Form.Label>
                                            <i
                                                className="bi bi-info-circle-fill ms-1"
                                                style={{ color: "red" }}
                                            ></i>
                                        </Form.Label>
                                    </OverlayTrigger>
                                    <Form.Control
                                        type="text"
                                        placeholder="V d: techcombank"
                                        value={newAccount.bankName}
                                        onChange={(e) =>
                                            updateNewAccount(
                                                "bankName",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group>
                                    <Form.Label className="mt-1">
                                        Link Bi Vit Minh Chng
                                    </Form.Label>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={
                                            <Tooltip>
                                                Link bi vit t gic hnh vi
                                                la o.  ngi xem bit r
                                                thc, h. Trnh trng hp vu
                                                oan gin ha cho ngi bn chn
                                                chnh.
                                            </Tooltip>
                                        }
                                    >
                                        <Form.Label>
                                            <i
                                                className="bi bi-info-circle-fill ms-1"
                                                style={{ color: "red" }}
                                            ></i>
                                        </Form.Label>
                                    </OverlayTrigger>
                                    <Form.Control
                                        type="text"
                                        placeholder="V d: https://www.facebook.com/groups/266140539492082/permalink/390975053745296/"
                                        value={newAccount.evidenceLink}
                                        onChange={(e) =>
                                            updateNewAccount(
                                                "evidenceLink",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                        </Row>
                        <Row className="justify-content-center mt-4">
                            <Col md={12}>
                                <Form>
                                    <Form.Group controlId="textArea">
                                        <Form.Label>
                                            Li khuyn tt p v cnh tnh cho
                                            ngi i sau:
                                        </Form.Label>
                                        <Form.Control
                                            as="textarea"
                                            rows={3}
                                            placeholder="Ngi vit khng nn, khng c dng nhng t th tc v khim nh v.v."
                                            value={newAccount.advice}
                                            onChange={(e) =>
                                                updateNewAccount(
                                                    "advice",
                                                    e.target.value
                                                )
                                            }
                                        />
                                    </Form.Group>
                                </Form>
                            </Col>
                        </Row>
                        <Row className="mt-2">
                            <Col xs={12} md={12}>
                                <Button
                                    variant="success"
                                    className="w-100"
                                    onClick={addAccount}
                                >
                                    Thm vo danh sch gi{" "}
                                    <i className="bi bi-plus-square-fill"></i>
                                </Button>
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
            </div>
            <Row>
                <Col md={3}>
                    <Form.Group controlId="mathProblem">
                        <Form.Label>
                            Nhp p n  c gi: {num1} + {num2} = ?
                        </Form.Label>
                        <Form.Control
                            type="number"
                            value={userAnswer}
                            onChange={(e) => setUserAnswer(e.target.value)}
                        />
                    </Form.Group>
                    <Button
                        className="mb-3 mt-2"
                        variant="warning"
                        onClick={handleSubmit}
                        disabled={loading}
                    >
                        Gi i danh sch
                        <i className="bi bi-send-arrow-up-fill ms-2"></i>
                    </Button>
                </Col>
                <span>
                    Mi ngi nn xem xt li thng tin trc khi gi. V khng
                    th xa, hay chnh sa.
                </span>
            </Row>
            {accounts.map((account, index) => (
                <Card className="mb-3" key={index}>
                    <Card.Body>
                        <Row>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group controlId={`accountNumber${index}`}>
                                    <Form.Label>S Ti Khon</Form.Label>
                                    <Form.Control
                                        type="text"
                                        disabled={!account.isEditing}
                                        value={account.accountNumber}
                                        onChange={(e) =>
                                            updateAccount(
                                                index,
                                                "accountNumber",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group controlId={`accountName${index}`}>
                                    <Form.Label>Tn Ch Ti Khon</Form.Label>
                                    <Form.Control
                                        disabled={!account.isEditing}
                                        type="text"
                                        value={account.accountName}
                                        onChange={(e) =>
                                            updateAccount(
                                                index,
                                                "accountName",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group controlId={`bankName${index}`}>
                                    <Form.Label>
                                        Tn H Thng Thanh Ton
                                    </Form.Label>
                                    <Form.Control
                                        disabled={!account.isEditing}
                                        type="text"
                                        value={account.bankName}
                                        onChange={(e) =>
                                            updateAccount(
                                                index,
                                                "bankName",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={12} xl={3}>
                                <Form.Group controlId={`evidenceLink${index}`}>
                                    <Form.Label>
                                        Link Bi Vit Minh Chng
                                    </Form.Label>
                                    <Form.Control
                                        disabled={!account.isEditing}
                                        type="text"
                                        value={account.evidenceLink}
                                        onChange={(e) =>
                                            updateAccount(
                                                index,
                                                "evidenceLink",
                                                e.target.value
                                            )
                                        }
                                    />
                                </Form.Group>
                            </Col>
                        </Row>
                        <Row className="justify-content-center mt-4">
                            <Col md={12}>
                                <Form>
                                    <Form.Group controlId="textArea">
                                        <Form.Label>
                                            Li khuyn tt p cho ngi i sau:
                                        </Form.Label>
                                        <Form.Control
                                            as="textarea"
                                            disabled={!account.isEditing}
                                            rows={3}
                                            placeholder="Ngi vit khng nn, khng c dng nhng t th tc v khim nh v.v."
                                            value={account.advice}
                                            onChange={(e) =>
                                                updateAccount(
                                                    index,
                                                    "advice",
                                                    e.target.value
                                                )
                                            }
                                        />
                                    </Form.Group>
                                </Form>
                            </Col>
                        </Row>

                        <Row className="mt-2">
                            <Col xs={12} md={6}>
                                <Button
                                    variant="danger"
                                    className="w-100 mt-2"
                                    onClick={() => deleteAccount(index)}
                                >
                                    Xa
                                </Button>
                            </Col>
                            <Col xs={12} md={6}>
                                <Button
                                    variant={
                                        account.isEditing
                                            ? "success"
                                            : "warning"
                                    }
                                    className="w-100 mt-2"
                                    onClick={() => {
                                        if (account.isEditing) {
                                            validateAndSaveAccount(index).catch(
                                                (error) => {
                                                    // Error handling already done in validateAndSaveAccount
                                                    // Do not toggle edit mode here, stay in edit mode until the update is successful
                                                }
                                            );
                                        } else {
                                            // Nu khng  trng thi chnh sa, ch chuyn sang trng thi chnh sa
                                            toggleEdit(index);
                                        }
                                    }}
                                >
                                    {account.isEditing ? "Cp nht" : "Sa"}
                                </Button>
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
            ))}

            <Modal show={showModal} onHide={() => setShowModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Xc nhn xa</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    Bn c chc chn mun xa ti khon ny khng?
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowModal(false)}
                    >
                        Hy
                    </Button>
                    <Button variant="danger" onClick={confirmDelete}>
                        Xa
                    </Button>
                </Modal.Footer>
            </Modal>
        </Container>
    );
};

export default PostScam;
