import Nav from "react-bootstrap/Nav";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import classNames from "classnames/bind";
import { jwtDecode } from "jwt-decode";
import styles from "./MainHeader.module.scss";
import { useState } from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { requestLogout } from "../../../redux/requestApi/userAccountThunk";
import { logOut } from "../../../redux/reducer/userAccountSlice";
import userAvatar from "./../../../images/user.png";

// Bind styles using classnames
// Gn kt cc styles s dng classnames
const cx = classNames.bind(styles);

// MainHeader component for the application header
// Component MainHeader cho header ca ng dng
function MainHeader() {
    const userAccount = useSelector((state) => state.user?.userAccount);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [isActive, setIsActive] = useState(false);

    // Decode the JWT to extract user roles
    let userRole = "";
    if (userAccount?.accessToken) {
        const decodedToken = jwtDecode(userAccount.accessToken);
        userRole = decodedToken.roles.roleName; // Correcting the path to the role name
    }
    const handleOnClick = () => setIsActive(!isActive);

    const handleLogout = () => {
        dispatch(requestLogout());
        dispatch(logOut());
        navigate("/");
    };

    return (
        <Navbar expand="lg" id="top-of-page" className="bg-body-tertiary">
            <Container>
                <Navbar.Brand as={Link} to={"/"}>
                    Check STK La o
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className={cx("align-items-center")}>
                        <Nav.Item>
                            <Nav.Link as={Link} to={"/to-cao-lua-dao"}>
                                T co la o
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link as={Link} to={"/thong-tin-chinh"}>
                                Hng dn s dng
                            </Nav.Link>
                        </Nav.Item>
                        <NavDropdown
                            title={
                                <button
                                    onClick={handleOnClick}
                                    className={cx("avatar-button")}
                                >
                                    <img
                                        src={userAvatar}
                                        className={cx(
                                            "rounded-circle",
                                            "avatar-image"
                                        )}
                                        alt="Avatar"
                                    />
                                </button>
                            }
                            id="nav-dropdown"
                            className="nav-dropdown"
                        >
                            {userAccount?.accessToken && (
                                <>
                                    {userAccount?.accessToken &&
                                        userRole === "admin" && (
                                            <NavDropdown.Item
                                                as={Link}
                                                to="/admin"
                                            >
                                                Qun l ngi dng
                                            </NavDropdown.Item>
                                        )}
                                    <NavDropdown.Item
                                        onClick={() => handleLogout()}
                                    >
                                        ng xut
                                    </NavDropdown.Item>
                                </>
                            )}
                            {userAccount?.accessToken ? (
                                ""
                            ) : (
                                <>
                                    <NavDropdown.Item
                                        as={Link}
                                        to={"/dang-nhap"}
                                    >
                                        ng nhp
                                    </NavDropdown.Item>
                                    <NavDropdown.Item as={Link} to={"/dang-ky"}>
                                        ng k
                                    </NavDropdown.Item>
                                </>
                            )}
                        </NavDropdown>
                        <Nav.Item>
                            Xin Cho{" "}
                            <span className="fw-bold">
                                {userAccount?.accessToken &&
                                userAccount.fullName
                                    ? userAccount.fullName
                                    : "Bn"}
                            </span>
                        </Nav.Item>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default MainHeader;
