import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Table, Form, Modal } from "react-bootstrap";
import {
    requestBanAccount,
    requestDeleteUser,
    requestGetFilteredUsers,
} from "../../../redux/requestApi/usersApiThunk";
import ReactPaginate from "react-paginate";
import { useDebounce } from "../../../HooksCustomer/debounce";
import className from "classnames/bind";
import { useNavigate } from "react-router-dom";
import styles from "./Admin.module.scss";
import { setIdInfoUser } from "../../../redux/reducer/postsApiSlice";
import { requestGetInfoUser } from "../../../redux/requestApi/postsApiThunk";

const cx = className.bind(styles);

// Admin component for user management
// Component Admin  qun l ngi dng
const Admin = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    // Get search results and total users from Redux store
    // Ly kt qu tm kim v tng s ngi dng t Redux store
    const searchResults = useSelector((state) => state?.users?.searchResults);
    const totalUsers = useSelector((state) => state?.users?.totalUsers);

    // State for search keyword
    // State cho t kha tm kim
    const [searchKeyword, setSearchKeyword] = useState("");

    // State for pagination control
    // State  iu khin phn trang
    const [pageCount, setPageCount] = useState(0);
    const [itemOffset, setItemOffset] = useState(0);

    // Number of users per page
    // S ngi dng mi trang
    const usersPerPage = 5;
    const [currentPage, setCurrentPage] = useState(0); // Trang hin ti

    // State for modal controls
    // State  iu khin modal
    const [showModal, setShowModal] = useState(false);
    const [selectedUser, setSelectedUser] = useState(null);
    const [showBanModal, setShowBanModal] = useState(false);
    const [userToBan, setUserToBan] = useState(null);

    // Debounce the search keyword to reduce API calls
    // Debounce t kha tm kim  gim s ln gi API
    const debouncedKeyword = useDebounce(searchKeyword, 500);

    // Load user list when search keyword or page offset changes
    // Ti danh sch ngi dng khi t kha tm kim hoc v tr trang thay i
    useEffect(() => {
        dispatch(
            requestGetFilteredUsers({
                searchKeyword: debouncedKeyword,
                limit: usersPerPage,
                offset: itemOffset,
            })
        );
    }, [debouncedKeyword, itemOffset, dispatch, usersPerPage]);

    // Calculate page count based on total users
    // Tnh ton s trang da trn tng s ngi dng
    useEffect(() => {
        setPageCount(Math.ceil(totalUsers / usersPerPage));
    }, [totalUsers, usersPerPage]);

    // Handle page click event for pagination
    // X l s kin click  phn trang
    const handlePageClick = (event) => {
        const newOffset = event.selected * usersPerPage;
        setItemOffset(newOffset);
        setCurrentPage(event.selected);
    };

    // Handle delete account request
    // X l yu cu xa ti khon
    const handleDelete = (userId) => {
        setShowModal(true);
        setSelectedUser(userId);
    };

    // Confirm and perform account deletion
    // Xc nhn v thc hin xa ti khon
    const confirmDelete = () => {
        dispatch(requestDeleteUser({ id: selectedUser }))
            .then(() => {
                // Reload user list after successful deletion
                // Ti li danh sch ngi dng sau khi xa thnh cng
                dispatch(
                    requestGetFilteredUsers({
                        searchKeyword: debouncedKeyword,
                        limit: usersPerPage,
                        offset: itemOffset,
                    })
                );
            })
            .catch((error) => {
                console.error("Error deleting user:", error);
            });
        setShowModal(false);
        setSelectedUser(null);
    };

    // Handle ban account request
    // X l yu cu ban ti khon
    const handleBanClick = (userId) => {
        setUserToBan(userId);
        setShowBanModal(true);
    };

    // Confirm and perform account ban
    // Xc nhn v thc hin ban ti khon
    const confirmBan = () => {
        dispatch(requestBanAccount({ id: userToBan }))
            .then(() => {
                // Update user list or notify user has been banned
                // Cp nht danh sch ngi dng hoc thng bo ngi dng  b ban
                setShowBanModal(false);
                setUserToBan(null);
                dispatch(
                    requestGetFilteredUsers({
                        searchKeyword: debouncedKeyword,
                        limit: usersPerPage,
                        offset: itemOffset,
                    })
                );
            })
            .catch((error) => {
                console.error("Error banning user:", error);
                setShowBanModal(false);
                setUserToBan(null);
            });
    };

    const handleViewInfoUser = (idUser) => {
        dispatch(requestGetInfoUser({ idUser: idUser }))
            .then(() => {
                dispatch(setIdInfoUser(idUser));
                navigate("/thong-tin-nguoi-dung");
            })
            .catch((error) => {});
    };

    return (
        <div className="container">
            <h2 className="mt-4 mb-4">Qun l ngi dng</h2>
            <Form.Control
                type="text"
                placeholder="Search"
                className="mr-sm-2 mb-2"
                value={searchKeyword}
                onChange={(e) => setSearchKeyword(e.target.value)}
            />
            <Table striped bordered hover>
                <thead>
                    <tr className={cx("header")}>
                        <th>ID</th>
                        <th>Email</th>
                        <th>H Tn</th>
                        <th>Hnh ng</th>
                    </tr>
                </thead>
                <tbody>
                    {searchResults.map((user, index) => (
                        <tr key={`user-${index}`} className={cx("body")}>
                            <td>
                                {user.groupId === 3 ? (
                                    <del>{user.id}</del>
                                ) : (
                                    user.id
                                )}
                            </td>
                            <td>
                                {user.groupId === 3 ? (
                                    <del>{user.email}</del>
                                ) : (
                                    user.email
                                )}
                            </td>
                            <td>
                                {user.groupId === 3 ? (
                                    <del>{user.fullName}</del>
                                ) : (
                                    user.fullName
                                )}
                            </td>
                            <td className={cx("action")}>
                                <Button
                                    onClick={() => handleViewInfoUser(user.id)}
                                    className="me-2 mt-1"
                                >
                                    Xem cc bi post
                                </Button>
                                <Button
                                    variant="warning"
                                    className="me-2 mt-1"
                                    disabled={user.groupId === 3}
                                    onClick={() => handleBanClick(user.id)}
                                >
                                    Ban ti khon
                                </Button>
                                <Button
                                    variant="danger"
                                    className="me-2 mt-1"
                                    onClick={() => handleDelete(user.id)}
                                >
                                    Xa ti khon
                                </Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
            <div className="d-flex justify-content-center">
                {pageCount > 0 && (
                    <ReactPaginate
                        nextLabel=">"
                        onPageChange={handlePageClick}
                        pageRangeDisplayed={3}
                        marginPagesDisplayed={2}
                        pageCount={pageCount}
                        previousLabel="<"
                        forcePage={currentPage}
                        pageClassName={cx("page-item")}
                        pageLinkClassName={cx("page-link")}
                        previousClassName={cx("page-item")}
                        previousLinkClassName={cx("page-link")}
                        nextClassName={cx("page-item")}
                        nextLinkClassName={cx("page-link")}
                        breakLabel="..."
                        breakClassName={cx("page-item", "break")}
                        breakLinkClassName={cx("page-link")}
                        containerClassName={cx("pagination")}
                        activeClassName={cx("active")}
                        renderOnZeroPageCount={null}
                    />
                )}
            </div>
            <Modal show={showModal} onHide={() => setShowModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Xc nhn xa</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    Bn c chc chn mun xa ti khon ny khng?
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowModal(false)}
                    >
                        Hy
                    </Button>
                    <Button variant="danger" onClick={confirmDelete}>
                        Xa Ti Khon
                    </Button>
                </Modal.Footer>
            </Modal>
            <Modal show={showBanModal} onHide={() => setShowBanModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Xc nhn ban</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    Bn c chc chn mun ban ti khon ny khng?
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowBanModal(false)}
                    >
                        Hy
                    </Button>
                    <Button variant="warning" onClick={confirmBan}>
                        Ban ti khon
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    );
};

export default Admin;
