import { useState, useEffect } from "react";

// Custom hook to debounce a value
// Hook ty chnh  debounce mt gi tr
export function useDebounce(value, delay) {
    const [debouncedValue, setDebouncedValue] = useState(value);

    // Effect to handle debouncing logic
    // Effect  x l logic debounce
    useEffect(() => {
        const handler = setTimeout(() => setDebouncedValue(value), delay);

        // Cleanup function to clear the timeout
        // Hm dn dp  xa timeout
        return () => clearTimeout(handler);
        // eslint-disable-next-line react-hooks/exhaustive-deps
        // Add delay to the dependency array to handle changes in delay
        // Thm delay vo mng dependencies  x l thay i trong delay
    }, [value]);

    return debouncedValue;
}
